.menu-item {
  min-width: 30%;
  height: 290px;
  border-radius: 10px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 7.5px 15px;
  overflow: hidden;
  position: relative;
  // border: 3px solid rgb(165, 165, 165);

  &:hover {
    cursor: pointer;

    & .background {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
  }

  .background {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background-position: center;
    background-size: cover;
  }

  .bg-gradient {
    width: 100%;
    height: 50%;
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.89) 100%
    );
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 );
    top: 145px;
    position: absolute;
  }

  .title{
    position: absolute;
    top: 83%;
    background-image: -webkit-gradient(linear,0% 0%,25% 100%,from(#dedf7e),to(#ff6600));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 12px;
    width: 100%;
    margin-left: 5%;
  }


}

@media only screen and (max-width: 600px) {

  .menu-item {
    width: 100%;
  }

  .bg-gradient {
    top: 144px;
  }

}
